import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MatDialog, MatDialogConfig} from '@angular/material/dialog';
import {PopupComponent} from '../../popup/popup.component';


@Component({
    selector: 'app-contacts',
    templateUrl: './contacts.component.html',
    styleUrls: ['./contacts.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class ContactsComponent implements OnInit {
    contactForm: FormGroup;
    submitted = false;

    stateShowBtn = {
        selectState: false,
        checkboxState: false
    };

    foods = [
        {value: '1', viewValue: ' '},
        {value: '2', viewValue: '  '},
        {value: '3', viewValue: ' '},
        {value: '4', viewValue: ''},
        {value: '5', viewValue: ''},
        {value: '6', viewValue: ' '},
    ];

    constructor(private formBuilder: FormBuilder, public matDialog: MatDialog) {
    }

    ngOnInit() {
        this.contactForm = this.formBuilder.group({
            firstName: ['', [Validators.required]],
            lastName: ['', [Validators.required]],
            fatherName: ['', [Validators.required]],
            email: ['', [Validators.required, Validators.email]],
            phone: ['', [Validators.required]],
            comment: ['']
        });

    }

    // convenience getter for easy access to form fields
    get f() {
        return this.contactForm.controls;
    }

    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.contactForm.invalid) {
            return;
        }
        this.openDialog();
        this.clearForm();
    }

    changeOnSelect() {
        this.stateShowBtn.selectState = true;
    }

    checkBoxChange(event) {
        this.stateShowBtn.checkboxState = event.target.checked;
    }

    openDialog(): void {
        const dialogRef = this.matDialog.open(PopupComponent, {
            data: {typePopup: 'info', title: '!', description: '    . <br>    &nbsp;.'}
        });
    }

    clearForm() {
        this.contactForm.reset();
    }
}
